@font-face {
  font-family: 'Comic-Cat';
  font-style: normal;
  font-weight: normal;
  src: local('Comic-Cat'), url('font/Comic-Cat.woff') format('woff');
}

html{
  box-sizing: border-box;
}

*,
*::after,
*::before{
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}




body {
  font-family: 'Comic-Cat', sans-serif;
  background-color: #f1f1ff;

}

.battlefield-table {
  margin: 0;
  border-collapse: collapse;
  display: inline-block;
  position: relative;

}
.battlefield-table--enemy {
  cursor: url("img/sigth.png"), pointer;
}
.battlefield-table--disabled {
  opacity: 0.5;
  cursor: url("img/err.png"), pointer;
}

.game {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.game__title {
  text-align: center;
  margin-bottom: 80px;
  letter-spacing: 1px;
}

.game__inner {

}

.game__content {
  display: flex;
}

.settings {
  margin-right: 70px;
}

.rotate {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 150px;

}

.rotate__ship {
  width: 120px;
  height: 40px;
  border: 2px solid rgba(59, 71, 255, 0.85);
  transform: rotate(0);
  transition: transform 0.2s linear;
  border-radius: 2px;
}

.rotate__ship--vertical {
  transform: rotate(90deg);
}

.cell {
  position: relative;
  border: 2px solid #bacbff;
  padding: 0;
  width: 43px;
  height: 43px;
  background-color: #f5f5ff;
}

.dot {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ship {
  position: absolute;
  top: -1px;
  left: -1px;
  border: 3px solid rgba(59, 71, 255, 0.85);
  user-select: none;
  width: 43px;
  height: 43px;
}

.ship--start {
  border-right: none;
}

.ship--end {
  border-left: none;
}

.ship--middle {
  border-left: none;
  border-right: none;
}

.ship--middle-vertical {
  border-top: none;
  border-bottom: none;
}


.ship--start-vertical {
  border-bottom: none;
}

.error {
  border-color: red;
}

.ship--end-vertical {
  border-top: none;

}

.box {
  width: 50px;
  height: 50px;
  background-color: red;
}

.ships__list {
  padding: 0;
  margin-bottom: 50px;

}

.ships__item {
  border-radius: 2px;
  border: 2px solid rgba(59, 71, 255, 0.85);
}

.ships__item + .ships__item {
  margin-left: 10px;
}

.ships__row {
  display: flex;
}

.ships__row + .ships__row {
  margin-top: 15px;
}

.damaged {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("./img/x.png");
}

.alive {
  border: none;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("./img/x.png");
}


.sunk {
  border-color: red;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("./img/x.png");
}

.table {
  display: flex;
}

.table__inner {
  position: relative;
  font-size: 18px;
  color: #5e75d0;
}

.table__inner + .table__inner {
  margin-left: 50px;
}

.x-cells {
  width: 432px;
  position: absolute;
  display: flex;
  list-style: none;
  justify-content: space-around;
  left: 0;
  top: -25px;
  padding: 0;
}

.y-cells {
  height: 432px;
  position: absolute;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  list-style: none;
  left: -22px;
  top: 0px;
  padding: 0;
}